.page-footer--without-map.page-footer::before {
  display: none;
}

.page-footer--without-map {
  padding-top: 300px;
  background-color: $white;

  @media (max-width: 1400px) {
    padding-top: 320px;
  }

  @include mobile {
    background-color: $bg-color;
  }

  .page-footer__container {
    background-color: $bg-color;
  }

  .page-footer__wrapper {
    padding-top: 0;
  }

  .page-footer__contacts {
    margin: 0 16px 11px 0;

    @media (max-width: 1400px) {
      margin: 0 30px -8px auto;
    }

    @media (max-width: 940px) and (min-width: 768px) {
      margin: 0 0 -8px auto;
    }

    @include mobile {
      margin: 0 15px 46px;
    }
  }

  .page-footer__logo {
    margin-bottom: 154px;

    @media (max-width: 1400px) {
      margin-bottom: 36px;
    }

    @include mobile {
      margin-bottom: 0;
    }
  }

  .social__list {
    margin: 0 0 28px;

    @media (max-width: 1400px) {
      margin: 0 0 21px;
    }
  }

  .page-footer__modal-wrap {
    margin: -287px 0 0 17px;

    @media (max-width: 1400px) {
      margin: -302px 0 32px 0;
    }

    @include mobile {
      margin: -302px 0 21px;
    }
  }

  .page-footer__modal {
    border: 1px solid #e9e9e9;

    @include mobile {
      border: none;
    }
  }
}



